body {
  overflow-x: hidden;
}
.modal-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.ReactModal__Content {
  padding: 0px !important;
  max-height: 80vh !important;
  max-width: 80vw !important;
  overflow: hidden !important;
  height: 50vh;
}
@media (max-width: 500px) {
  .ReactModal__Content {
    padding: 0px !important;
    width: 90vw !important;
    height: 90vh !important;
    max-height: 100% !important;
    max-width: 100% !important;
  }
}
.close-container {
  position: absolute;
  top: 0px;
  left: 10px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  z-index: 99;
}

.leftright {
  height: 4px;
  width: 30px;
  position: absolute;
  margin-top: 24px;
  background-color: #88db65;
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all 0.3s ease-in;
}

.rightleft {
  height: 4px;
  width: 30px;
  position: absolute;
  margin-top: 24px;
  background-color: #88db65;
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all 0.3s ease-in;
}
.close {
  margin: 60px 0 0 5px;
  position: absolute;
}

.close-container:hover .leftright {
  transform: rotate(-45deg);
  background-color: #4d8a33;
}
.close-container:hover .rightleft {
  transform: rotate(45deg);
  background-color: #4d8a33;
}
.close-container:hover label {
  opacity: 1;
}
.myButton {
  box-shadow: inset 0px 1px 0px 0px #f29c93;
  background: linear-gradient(to bottom, #fe1a00 5%, #ce0100 100%);
  background-color: #fe1a00;
  border-radius: 6px;
  border: 1px solid #d83526;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;

  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #b23e35;
  position: absolute;
  bottom: 40px;
  right: 60px;
}
.myButton:hover {
  background: linear-gradient(to bottom, #ce0100 5%, #fe1a00 100%);
  background-color: #ce0100;
}

.myButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.rep-button {
  box-shadow: inset 0px 1px 0px 0px #f29c93;
  background: linear-gradient(to bottom, #fe1a00 5%, #ce0100 100%);
  background-color: #fe1a00;
  border-radius: 6px;
  border: 1px solid #d83526;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;

  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #b23e35;
}
.rep-button:hover {
  background: linear-gradient(to bottom, #ce0100 5%, #fe1a00 100%);
  background-color: #ce0100;
}
.button-group {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.button-group button:not(:last-child) {
  margin-right: 10px;
}
.slide-in-bottom {
  -webkit-animation: slide-in-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    both;
  animation: slide-in-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.form-down {
  position: absolute;
  bottom: -184px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -35%) !important;
  z-index: 999;
  width: 100vw;
  max-height: 50vh;
  height: 50vh;
  background-color: white;
  overflow-y: scroll;
  overflow-x: hidden;
}

@keyframes slide-in-bottom {
  0% {
    -webkit-transform: translateY(1000px);
    transform: translateY(1000px);
    opacity: 0;
    visibility: visible;
    z-index: 999;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slide-in-down {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(1000px);
    transform: translateY(1000px);
    opacity: 0;
    visibility: hidden;
    position: absolute;
    z-index: -1;
  }
}
.slide-in-down {
  -webkit-animation: slide-in-down 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    both;
  animation: slide-in-down 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.modal-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  text-align: center;
  padding: 10px 20px;
}
.form-col {
  display: flex;
  flex-direction: column;
  padding: 10px 20px;
}
.form-col h2 {
  margin: 40px 0px;
  text-align: center;
  color: #4d8a33;
}
.row-slider {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;

  height: 100%;
}
.carousel-root {
  height: 100%;
  width: 100%;
}
.carousel-root .carousel .carousel-slider,
.slider-wrapper,
.slider,
.slider li,
.slider li div,
.carousel {
  width: 100% !important;
  height: 100% !important;
}
.carousel-root {
  height: 50% !important;
  overflow: hidden !important;
}
.control-dots {
  position: absolute;
  bottom: 15px;
  padding: 0;
}
.carousel-box div {
  width: 50%;
  height: 50%;
}
.thumbs-wrapper {
  display: none;
}
.carousel {
  height: 100% !important;
}
.carousel-box {
  display: flex;
  flex-direction: row;
  text-align: left;
  width: 98% !important;
}
@media (max-width: 500px) {
  .carousel-root {
    width: 94.8% !important;
  }
  .form-col h2 {
    margin-bottom: 0px;
  }
}
.form button {
  margin-bottom: 20px;
}
.modal-info div {
  margin-bottom: 50px;
}
.modal-info .button-group {
  margin-bottom: 40px;
}
@media (max-width: 800px) {
  .carousel-box {
    overflow-y: scroll !important;
    flex-direction: column !important;
    text-align: center !important;
    align-items: center !important;
    height: 50% !important;
  }
  .carousel-box div {
    height: auto !important;
  }
}
.green {
  box-shadow: inset 0px 1px 0px 0px #43a047;
  background-color: #4d8a33 !important;
  background: linear-gradient(to bottom, #43a047 5%, #4d8a33 100%);
  border: 1px solid #43a047;
  text-shadow: 0px 1px 0px #43a047;
  transition: all 0.5s;
}
.green:hover {
  background-color: #4d8a33 !important;
  transition: all 0.5s;
  background: linear-gradient(to bottom, #444444 5%, #444444 100%);
}
@media (max-width: 991px) {
  .myButton {
    bottom: 11px !important;
  }
}
@media (max-width: 500px) {
  .myButton {
    bottom: -54px !important;
  }
  .carousel-box div {
    width: 100% !important;
  }
  .form-down {
    transform: translate(-50%, -15%) !important;
  }
}
