.form-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

form {
  width: 100%;
  height: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}
input[type="textarea"] {
  margin: 20px auto !important;
  width: 40% !important;
}
.input-down {
  display: flex;
  flex-direction: column;
  align-content: center;
}

.input-down button {
  width: auto !important;
  margin: 0 auto !important;
}
form input {
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  width: auto !important;
  margin-right: 10px !important;
}
form button,
.chooseFileButton {
  text-transform: uppercase !important;
  outline: 0 !important;
  background: #4caf50 !important;
  width: 100% !important;
  border: 0 !important;
  padding: 15px !important;
  color: #ffffff !important;
  font-size: 14px !important;
  -webkit-transition: all 0.3 ease !important;
  transition: all 0.3 ease !important;
  cursor: pointer !important;
  border-radius: 0px !important;
  width: auto !important;
}
form button:hover,
form button:active,
form button:focus {
  background: #43a047;
}

.hidden {
  position: absolute;
  z-index: -1;
  top: -1000px;
  visibility: hidden;
  height: 0px !important;
}
.input-down {
  width: 100%;
}
.carousel-box span {
  color: #43a047;
}
@media (max-width: 500px) {
  input[type="textarea"] {
    width: 100% !important;
  }
}
